import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class TemplatesService extends AppClientService {
  constructor(private http: HttpClient) {
    super(http);
  }

  getTemplates(eduCompID: any, page: number, body: any) {
    return this.post(`Admin/getTemplates/${eduCompID}/${page}`, body);
  }

  getTemplateById<T>(templateId: any) {
    return this.get<T>(`template/returnTemplateById/${templateId}`);
  }

  deleteTemplate(id: any) {
    return this.get(`template/deleteTemplateById/${id}`);
  }

  PublishDepublishTemplate(id: any) {
    return this.post(`template/PublishDepublishTemplate/${id}`, null);
  }

  getPresets(eduCompId: any) {
    return this.get(`template/returnPresetsByEduCompId/${eduCompId}`);
  }

  addUpdateTemplate(data: any) {
    return this.post("template/addtemplate", data);
  }

  addTemplateDetails(data: any) {
    return this.post("AJAX/addTemplateDetail", data);
  }

  getQuestionDetails(templateId: any) {
    return this.get(`AJAX/returnTemplateDetails/${templateId}`);
  }

  DeleteDetails(id: any) {
    return this.get(`template/deleteTemplateDetailById/${id}`);
  }

  getTemplatePoints(templateId: any, page: any) {
    return this.get(
      `template/returnCustomPointTemplateByTemplateId/${templateId}/${page}`
    );
  }

  addTemplatePoint(body: any) {
    return this.post("template/CustomTemplatePoint", body);
  }

  deleteTemplatePoint(pointId: any) {
    return this.delete(`template/DeleteCustomTemplatePoint/${pointId}`);
  }
}
