  getAllExams(p: number) {
    this.curPage=p;
    this.spinner.show();

    // this.page?.setValue(p);
    // this.myForm.controls["EduCompId"].setValue(this.EduCompId);
    let data = {
      typeId: this.type,
      page: p,
      EduCompId: this.EduCompId,
      subjectId: this.subId,
      examName: this.examNameCtrl?.value,
    };
    this.examService.getAllNormalExams(data).subscribe((response: any) => {
      if (response) {
        this.dataSource = new MatTableDataSource(response.examList);
        this.exams = response.examList;
        this.totalItems = response.totalCount;
        this.startSortingTable();
      }
      this.spinner.hide();
    });
  }

  removeExam(exam: Exam) {
    this.examService.deleteExam<any>(exam.id).subscribe((response) => {
      if (response) {
        this.toastr.success("General.DeleteSuccessfully");
        if (this.dataSource.data.length == 1 && this.curPage > 1) {
          // if last item in list: get previous page
          this.getAllExams(this.curPage - 1);
          this.paginator.pageIndex = this.curPage - 1;
        } else {
          // if not last item in list: get current page
          this.getAllExams(this.curPage);

        }
      }
    });
  }

  openDeleateDialog(exam: Exam): void {
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: exam.exam_ar_name,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) {
          this.removeExam(exam);
        }
      });
  }

  onPublishExam(exam: IExamViewModel) {
    this.examService
      .publishExam<IExamViewModel>(exam.id)
      .subscribe((response) => {
        if (response) {
          let i = this.exams.findIndex((e) => e === exam);
          this.exams[i].isPublish = !this.exams[i].isPublish;
          // for (const obj of this.exms) {
          //   if (obj.id === exam.id) {
          //     obj.isPublish = !obj.isPublish;
          //     break;
          //   }
          // }
          this.toastr.success("General.Done");
        }
      });
  }

