import { TemplatesCustomePointsComponent } from "./templates/templates-custome-points/templates-custome-points.component";
import { AddTemplateComponent } from "./templates/add-template/add-template.component";
import { AddQuestionComponent } from "./questions-banck/add-question/add-question.component";
import { QuestionsIndexComponent } from "./questions-banck/questions-index/questions-index.component";
import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
import { IsAdminGuard } from "app/shared/services/auth/Guards/roles/is-admin.guard";
import { AddExamComponent } from "./exams/add-exam/add-exam.component";
import { ExamsIndexComponent } from "./exams/exams-index/exams-index.component";
import { TemplatesIndexComponent } from "./templates/templates-index/templates-index.component";
import { CorrectionIndexComponent } from "./exam-correction/correction-index/correction-index.component";
import { ExamPreviewComponent } from "./exams/exam-preview/exam-preview.component";
import { ExamsCustomePointsComponent } from "./exams/exams-custome-points/exams-custome-points.component";
import { SurveysIndexComponent } from "./surveys/surveys-index/surveys-index.component";
import { AddSurveyComponent } from "./surveys/add-survey/add-survey.component";

const routes: Routes = [
  {
    path: "",
    redirectTo: "exam",
    pathMatch: "full",
  },

  {
    path: "exam-preview/:examId/:type",
    component: ExamPreviewComponent,
  },

  {
    path: "exam/:courseId/:typeId",
    children: [
      {
        path: "",
        redirectTo: "index",
        pathMatch: "full",
      },
      {
        //http://localhost:4200/dashboard/exams-setup/exam/add-exam/3?functionId=23
        path: "index",
        component: ExamsIndexComponent,
      },

      {
        path: "add-exam",
        component: AddExamComponent,
      },
      {
        path: "exam-points/:examId",
        component: ExamsCustomePointsComponent,
      },
    ],
  },
  {
    path: "survey/:courseId/:typeId",
    data: {
      breadcrumb: "Navbar.Surveys",
      view: "survey",
    },
    children: [
      {
        path: "",
        redirectTo: "index",
        pathMatch: "full",
      },
      {
        path: "index",
        component: SurveysIndexComponent,
        data: {
          breadcrumb: "Navbar.SurveyList",
          view: "survey-index",
        },
      },

      {
        path: "add-survey",
        component: AddSurveyComponent,
        data: {
          breadcrumb: "Navbar.newesurvay",
          view: "add-survey",
        },
      }
    ],
  },

  {
    path: "question-bank",
    children: [
      {
        path: "",
        redirectTo: "index",
        pathMatch: "full",
      },
      {
        path: "index",
        component: QuestionsIndexComponent,
      },
      {
        path: "add-question",
        component: AddQuestionComponent,
      },
    ],
  },

  {
    path: "templates",
    children: [
      { path: "", redirectTo: "index", pathMatch: "full" },
      { path: "index", component: TemplatesIndexComponent },
      { path: "add-template", component: AddTemplateComponent },
      {
        path: "template-points/:templateId",
        component: TemplatesCustomePointsComponent,
      },
    ],
  },
  {
    path: "exam-correction",
    children: [
      { path: "", redirectTo: "index", pathMatch: "full" },
      { path: "index", component: CorrectionIndexComponent },
      // { path: "add-template", component: AddTemplateComponent },
    ],
  },
  // { path: "templates-index", component: TemplatesIndexComponent },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class ExamsRoutingModule {}
